<template>
  <div class="map__wrapper">
    <l-map :zoom="zoom" :center="center">
      <l-tilelayer :url="url" :attribution="attribution"></l-tilelayer>
      <div v-if='$route.name == "ecology"'>
        <div v-if='showPrudesTrees'>
          <l-marker v-for="marker in prudesTrees" :key="marker.index" @click="markerClick(marker)"
          :lat-lng="{lat: marker.lat, lng: marker.lan}" :icon="prudesIcon"
        >
        </l-marker>
        </div>
        <div v-if='showNewTrees'>
            <l-marker v-for="marker in newTrees" :key="marker.index" @click="markerClick(marker)"
            :lat-lng="{lat: marker.lat, lng: marker.lan}" :icon="newIcon"
          >
          </l-marker>
        </div>
        <div v-if='showRemovedTrees'>
          <l-marker v-for="marker in removedTrees" :key="marker.index" @click="markerClick(marker)"
            :lat-lng="{lat: marker.lat, lng: marker.lan}" :icon="removedIcon"
          >
        </l-marker>
        </div>
      </div>
      <div v-if='$route.name == "radiation"'>
          <l-marker v-for="marker in stations" :key="marker.index" @click="markerRadiationClick(marker)"
          :lat-lng="{lat: marker.coords.lat, lng: marker.coords.lan}" :icon="factoryIcon"
        >
        </l-marker>
      </div>
    </l-map>
  </div>
</template>

<script>
import {mapState, mapActions} from 'vuex';
import {LMap, LTileLayer, LMarker } from 'vue2-leaflet';
import L from 'leaflet';

// <div>Icons made by <a href="https://www.flaticon.com/authors/freepik" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a></div>

export default {
  data() {
    return {
      zoom: 8,
      center: L.latLng(48, 33),
      url:'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
      attribution:'&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      newIcon: L.icon({
        iconUrl: require('@/assets/new.png'),
        iconSize: [24, 24],
        iconAnchor: [12, 12],
      }),
      removedIcon: L.icon({
        iconUrl: require('@/assets/stump.png'),
        iconSize: [24, 24],
        iconAnchor: [12, 12],
      }),
      prudesIcon: L.icon({
        iconUrl :require('@/assets/prun.png'),
        iconSize: [24, 24],
        iconAnchor: [12, 12],
      }),
      factoryIcon: L.icon({
        iconUrl :require('@/assets/factory1.png'),
        iconSize: [24, 24],
        iconAnchor: [12, 12],
      }),
      showPrudesTrees: true,
      showNewTrees: true,
      showRemovedTrees: true
    }
  },
  components: {
    'l-map': LMap,
    'l-tilelayer' :LTileLayer,
    'l-marker': LMarker,
  },
  mounted() {
    setTimeout(function() { window.dispatchEvent(new Event('resize')) }, 250);
  },
  computed:
    mapState({
      prudesTrees ({ecology}) {
        return ecology.prunedTreesArray
      },
      newTrees ({ecology}) {
        return ecology.newTreesArray
      },
      removedTrees ({ecology}) {
        return ecology.removedTreesArray
      },
      activeMarker({ecology}) {
        return ecology.activeMarkerEcology
      },
      filterEcology({ecology}) {
        return ecology.filterEcology
      },
      stations({radiation}) {
        return radiation.stations
      },
    }),
  methods: {
    ...mapActions([
      'addActiveMarkerEcology',
      'addActiveMarkerRadiation'
    ]),
    markerClick (marker) {
      this.addActiveMarkerEcology(marker)
    },
    markerRadiationClick (marker) {
      this.addActiveMarkerRadiation(marker)
      console.log(marker, 'marker')
    },
    checkMarkers (val) {
      if (val.indexOf("Нові дереві") === -1) {
        this.showNewTrees = false
      } else this.showNewTrees = true
      if (val.indexOf("Обрізані дерева") === -1) {
        this.showPrudesTrees = false
      } else this.showPrudesTrees = true
      if (val.indexOf("Видалені дерева") === -1) {
        this.showRemovedTrees = false
      } else this.showRemovedTrees = true
    }
  },
  watch: {
    filterEcology: function (val) {
      console.log(val, 'val')
      this.checkMarkers(val)
    },
    '$route.name': function (val) {
      if (val === "ecology") {
        this.checkMarkers(this.filterEcology)
      }
    }
  },
}
</script>
<style lang="scss">
@import "~leaflet/dist/leaflet.css";
</style>
<style lang="scss" scoped>
.map {
  &__wrapper {
    width: 100%;
    height: 500px;
  }
}
.display-none {
  opacity: 0;
  display: inline;
}
</style>